import React from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBNavLink, MDBView, MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBMask, MDBCarouselCaption } from 'mdbreact';
import { BrowserRouter as Router } from 'react-router-dom';
import { COLORS, CSS } from '../assets/style';
import { Button } from '@material-ui/core';

const Image = require('../assets/icons/app_logo.png')

interface State {
  collapse: boolean
  isWideEnough: boolean
}

class Header extends React.Component<{}, State> {
  constructor(props: any) {
    super(props);
    this.state = {
      collapse: false,
      isWideEnough: false,
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse,
    });
  }

  carouselList = [{
    title: 'do you want to feel better?',
    description: 'Get help now with a nutritional therapy session to boost energy, improve your mood and tackle any ill health issues.',
    url: 'https://image.shutterstock.com/image-photo/panoramic-banner-focus-fresh-vegetables-260nw-1471020857.jpg',
    btnLabel: "Know More"
  },
  {
    title: 'easy book & pay online',
    description: 'Choose your time, pay & receive confirmation all in the comfort of your own home.',
    url: 'https://image.shutterstock.com/image-photo/diet-plant-based-protein-healthy-260nw-1691259316.jpg',
    btnLabel: "Know More"
  },
  {
    title: 'do you want weight loss results that last?',
    description: 'Learn how to lose weight and keep it off!',
    url: 'https://www.docdonald.com/wp-content/uploads/2018/06/nutrition-banner.jpg',
    btnLabel: "Know More"
  }
  ]

  render() {
    return (
      <header>
        <Router>
          <MDBNavbar style={{ ...CSS.navBar }} fixed="top" dark expand="md" scrolling>
            <MDBNavbarBrand href="/">
              <img alt="app logo" height="50px" src={String(Image.default)} />
              <div className="float-right ml-1">
                <h4 style={{ color: COLORS.app_green }} className="mb-0">Company Name</h4>
                <small style={{ color: COLORS.app_orange }}>Company Tagline</small>
              </div>
            </MDBNavbarBrand>
            {!this.state.isWideEnough && <MDBNavbarToggler onClick={this.onClick} />}
            <MDBCollapse isOpen={this.state.collapse} navbar>
              <MDBNavbarNav right>
                <MDBNavItem active>
                  <MDBNavLink className="text-dark pl-5 pr-5" to="#">Home</MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink className="text-dark pl-5 pr-5" to="#">About</MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink className="text-dark pl-5 pr-5" to="#">Service</MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink className="text-dark pl-5 pr-5" to="#">Plan</MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <Button size="large" style={{ ...CSS.btn }}>Book Appointment</Button>
                </MDBNavItem>
              </MDBNavbarNav>
            </MDBCollapse>
          </MDBNavbar>
        </Router>

        <MDBCarousel
          activeItem={1}
          length={3}
          showControls={true}
          showIndicators={true}
          className="z-depth-1"
          style={{ ...CSS.carousel }}
        >
          <MDBCarouselInner>
            {this.carouselList.map((listItem, index) => <MDBCarouselItem key={index} itemId={++index}>
              <MDBView>
                <img
                  className="d-block w-100"
                  src={listItem.url}
                  alt="First slide"
                />
                <MDBMask overlay="black-light" />
              </MDBView>
              <MDBCarouselCaption>
                <h3 className="h3-responsive text-white">{listItem.title}</h3>
                <p className="text-white">{listItem.description}</p>
                <div>
                  <Button className="mb-5" style={{ ...CSS.btn }}>{listItem.btnLabel}</Button>
                </div>
              </MDBCarouselCaption>
            </MDBCarouselItem>)}
          </MDBCarouselInner>
        </MDBCarousel>
      </header>
    );
  }
}

export default Header;